.review .container {
  margin-top: 3rem;
}

.slide {
  margin: 0 1rem;
  border-radius: 1rem;
  box-shadow: var(--shadow);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--bg1);
  padding: 3rem 7rem;
  cursor: pointer;
}

.slide-img {
  height: 90px;
  width: 90px;
  border-radius: 50%;
  overflow: hidden;
  margin-bottom: 1rem;
}

.slide-img img {
  width: 100%;
}

.slide h3 {
  color: var(--orange);
  margin-bottom: 1rem;
}

.slide p {
  text-align: center;
  color: var(--text2);
  width: 69%;
}

.slide-icon {
  color: var(--orange);
  font-size: 2rem;
}



.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: var(--bg1);

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

/* Mobile */
@media screen and (max-width: 480px) {
  .mySwiper {
    display: flex;
    flex-direction: column;
  }

  .slide p {
    font-size: 1.1rem;
    text-align: center;
    color: var(--text2);
    width: 100%;
  }
}
