.pricing-row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  grid-gap: 2rem;
  width: 100%;
  margin-top: 3rem;
}

.pricing-col {
  background-color: var(--bg1);
  border-radius: 1rem;
  box-shadow: var(--shadow);
  padding: 2rem 1.4rem;
  text-align: center;
  height: max-content;
}

.pricing-col .head {
  background-color: var(--bg2);
  padding: 0.4rem 0.8rem;
  padding: 1rem;
  border-radius: 1rem;
}

.pricing-col h1 span {
  font-weight: 300;
  font-size: 2rem;
}

.pricing-col li {
  font-weight: 300;
  color: var(--text2);
}

/* Tab */
@media screen and (max-width: 1040px) {
  .pricing-row {
    grid-template-columns: repeat(2, 1fr);
  }

  .pricing-col li {
    font-size: 1.7rem;
  }
}

/* Mobile */
@media screen and (max-width: 480px) {
  .pricing-row {
    grid-template-columns: 1fr;
  }
}
