.services-row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.2rem;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
}

.services-col {
  background-color: var(--bg1);
  padding: 0.8rem 1.6rem;
  height: fit-content;
  border-radius: 1rem;
  box-shadow: var(--shadow);
}

.card-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-icon {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background-color: var(--orange);
  display: flex;
  justify-content: center;
  align-items: center;
}

.icon {
  font-size: 2.6rem;
  color: var(--bg1);
}

.services-col h1 {
  color: var(--text2);
}

.services-col p {
  font-size: 1.4rem;
  color: var(--text2);
}

/* Tab */
@media screen and (max-width: 1040px) {
  .services-row {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* Mobile */
@media screen and (max-width: 480px) {
  .services-row {
    grid-template-columns: 1fr;
  }
}
