:root {
  --orange: #f79f1f;
  --text1: #444;
  --text2: #666;
  --bg1: #fff;
  --bg2: #eee;
  --shadow: 0px 0.5rem 1.5rem rgba(0, 0, 0, 0.1);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  text-decoration: none;
  text-transform: capitalize;
  transition: all 0.2s linear;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  line-height: 1.4;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-padding-top: 7rem;
}

body.active {
  --orange: #f79f1f;
  --text1: #fff;
  --text2: #eee;
  --bg1: #444;
  --bg2: #666;
}

::-webkit-scrollbar {
  width: 0.8rem;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: var(--orange);
  border-radius: 2rem;
}

body {
  background-color: var(--bg2);
}

.container {
  width: 86%;
  margin: 0 auto;
}

section .container {
  padding-top: 3rem;
}
.btn {
  background-color: var(--orange);
  padding: 1rem 3rem;
  color: var(--bg1);
  display: inline-block;
  border-radius: 1rem;
}

.btn:hover {
  letter-spacing: 0.2rem;
}

h1 {
  font-size: 4.2rem;
  margin-bottom: 1.2rem;
  color: var(--text1);
  position: relative;
}

h2 {
  font-size: 4rem;
  color: var(--text1);
  margin-bottom: 1.2rem;
}

h3 {
  font-size: 2.2rem;
  color: var(--text1);
}

p {
  font-size: 1.6rem;
  color: var(--text2);
  font-weight: 300;
  margin-bottom: 2rem;
}

ul {
  list-style-type: none;
}

li {
  margin: 2rem 0;
}

section {
  padding: 3rem 0;
}

section h2 {
  position: relative;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
}

section h2::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: 0.6rem;
  height: 8px;
  width: 65%;
  background-color: var(--orange);
  z-index: -1;
}

.review h2::after {
  width: 50%;
}

/* Tab */
@media screen and (max-width: 1040px) {
  html {
    font-size: 75%;
  }

  section .container {
    padding-top: 2rem;
  }
}

/* Mobile */
@media screen and (max-width: 480px) {
  html {
    font-size: 65%;
  }

  section .container {
    padding-top: 1rem;
  }
}
