.packages-boxes {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.4rem;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}

.packages-box {
  background-color: var(--bg1);
  border-radius: 1rem;
  height: fit-content;
  padding: 1.4rem;
  box-shadow: var(--shadow);
}

.packages-img {
  height: 200px;
  overflow: hidden;
  border-radius: 1rem;
}

.packages-img img {
  width: 100%;
}

.packages-text div {
  display: flex;
  align-items: center;
  margin: 1.6rem 0;
  gap: 1rem;
}

.packages-text div small {
  text-decoration: line-through;
}

/* Tab */
@media screen and (max-width: 1040px) {
  .packages-boxes {
    grid-template-columns: repeat(2, 1fr);
  }

  .packages-text div {
    margin: 1.2rem 0;
  }
}

/* Mobile */
@media screen and (max-width: 480px) {
  .packages-boxes {
    grid-template-columns: 1fr;
  }

  .packages-text div {
    margin: 1.2rem 0;
  }
}
