.blog-row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.2rem;
  justify-content: center;
  align-items: center;
}

.blog-col {
  background-color: var(--bg1);
  height: max-content;
  box-shadow: var(--shadow);
  padding: 1.6rem;
  border-radius: 1rem;
}

.blog-img {
  height: 200px;
  overflow: hidden;
  border-radius: 1rem;
  background-color: var(--bg2);
}

.blog-img img {
  width: 100%;
}

.blog-col h3 {
  margin-top: 0.5rem;
  font-size: 1.8rem;
}

.blog-col .btn {
  margin: 1rem 0;
}

.blog-col hr {
  display: block;
  height: 0.7px;
  width: 100%;
  margin: 1rem auto;
  background-color: var(--text1);
}

.col-date {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 300;
}

.colIcon {
  color: var(--orange);
  margin-right: 0.5rem;
}

.date,
.user {
  display: flex;
  align-items: center;
}

/* Tab */
@media screen and (max-width: 1040px) {
  .blog-row {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* Mobile */
@media screen and (max-width: 480px) {
  .blog-row {
    grid-template-columns: 1fr;
  }
}
