.hero-row {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

.hero-img {
  width: 100%;
}

/* Tab */
@media screen and (max-width: 1040px) {
  .hero-row {
    display: flex;
    flex-direction: column;
  }

  .hero-img {
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
  }
}

/* Mobile */
@media screen and (max-width: 480px) {
  .hero-row {
    display: flex;
    flex-direction: column;
    padding: 2rem 0;
    gap: 0;
  }

  .hero-img {
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
  }

  .hero-col .btn{
    width: 100%;
    text-align: center;
  }
}
