.footer {
  height: max-content;
  background-color: var(--bg1);
  overflow: hidden;
}

.footer-row {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(4, 1fr);
  justify-content: space-between;
}

.footer-col a {
  color: var(--text2);
  font-weight: 300;
}

.footer li {
  display: flex;
  align-items: center;
}

.mapPin {
  color: var(--orange);
  margin-right: 0.6rem;
}
.arrow {
  color: var(--orange);
  margin-right: 0.6rem;
}
.phone {
  color: var(--orange);
  margin-right: 0.6rem;
}
.links {
  color: var(--orange);
  margin-right: 0.6rem;
}

.footer a:hover {
  margin-left: 0.6rem;
}

.footer p {
  text-align: center;
  font-size: 2rem;
}

.footer hr {
  width: 86%;
  margin: 2rem auto;
  height: 1px;
  background-color: var(--text1);
}

.footer p a {
  color: var(--orange);
  font-size: 2rem;
}
.footer p a:hover {
  margin: 0;
}

/* Tab */
@media screen and (max-width: 1040px) {
  .footer-row {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* Mobile */
@media screen and (max-width: 480px) {
  .footer-row {
    grid-template-columns: 1fr;
  }
}
