.calender {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: space-between;
  align-items: center;
  grid-gap: 2rem;
  width: 100%;
  background-color: var(--bg1);
  padding: 2rem 4rem;
  border-radius: 1rem;
  box-shadow: var(--shadow);
}

.calender h4 {
  font-size: 1.7rem;
  font-weight: 300;
  margin-bottom: 0.5rem;
}

.calender input {
  width: 100%;
  background-color: var(--bg2);
  padding: 1rem 1.6rem;
  border-radius: 0.6rem;
}

.calender .btn {
  width: 100%;
  text-align: center;
}

/* Tab */
@media screen and (max-width: 1040px) {
  .calender {
    display: grid;
    grid-template-columns: 1fr;
    margin: 2rem 0;
  }
}

/* Mobile */
@media screen and (max-width: 480px) {
  html {
    font-size: 65%;
  }
}
