header {
  box-shadow: var(--shadow);
  height: 10vh;
  background-color: var(--bg1);
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  cursor: pointer;
}

.logoIcon {
  font-size: 3rem;
  margin-right: 0.3rem;
  color: var(--orange);
}

.logo h1 {
  font-size: 3rem;
  margin: 0;
}

.input {
  position: relative;
  display: flex;
  align-items: center;
}

.headerInput {
  background-color: var(--bg2);
  width: 500px;
  padding: 1rem 4rem;
  border-radius: 1rem;
}

.headerSearch {
  position: absolute;
  right: 20px;
  font-size: 2rem;
  color: var(--text1);
  cursor: pointer;
}

.icons {
  display: flex;
  align-items: center;
}

.icons div {
  height: 50px;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
  background-color: var(--bg2);
  margin: 0 0.5rem;
  position: relative;
  cursor: pointer;
}

.icons div:hover {
  background-color: var(--orange);
}

.icons div:hover .iconIcons {
  color: var(--bg1);
}

.iconIcons {
  color: var(--text1);
  font-size: 2rem;
  cursor: pointer;
}

/* Tab */
@media screen and (max-width: 1040px) {
  .icons div:nth-child(2),
  .icons div:nth-child(3) {
    display: none;
  }
  .icons div:first-child {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .headerInput {
    background-color: var(--bg2);
    width: 350px;
  }
}

/* Mobile */
@media screen and (max-width: 600px) {
  .icons div:nth-child(2),
  .icons div:nth-child(3) {
    display: none;
  }
  .icons div:first-child {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .headerInput {
    display: none;
  }
  .headerSearch {
    display: none;
  }
}
