.contact h2::after {
  left: 0;
}

.contact {
  height: max-content;
}

.contact .container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3rem;
}

.contact form {
  width: 70%;
  margin: 0 auto;
}

.contact form div {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  align-items: center;
  grid-gap: 2rem;
}

.contact form div input {
  background-color: var(--bg1);
  box-shadow: var(--shadow);
  padding: 1.4rem 0.8rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
}

.contact form textarea {
  border-radius: 1rem;
  padding: 1.4rem 0.8rem;
  width: 100%;
  height: 180px;
  background-color: var(--bg1);
}

.contact form div input::placeholder,
.contact form textarea::placeholder {
  color: var(--text2);
  font-weight: 300;
}

.contact button {
  margin: 1.2rem auto 0 auto;
  width: 100%;
  cursor: pointer;
}

/* Tab */
@media screen and (max-width: 1040px) {
  .contact form div {
    grid-template-columns: 1fr;
  }

  .contact form div input {
    margin-bottom: 1rem;
  }

  .contact form div {
    grid-gap: 1rem;
    justify-content: space-between;
  }
}

/* Mobile */
@media screen and (max-width: 480px) {
  .contact form {
    width: 100%;
    margin: 0 auto;
  }
  .contact form div {
    grid-template-columns: 1fr;
  }

  .contact form div input {
    margin-bottom: 1rem;
  }

  .contact form div {
    grid-gap: 1rem;
    justify-content: space-between;
  }
}
